import React from "react";
import {NavLink} from "react-router-dom";
import Logo from "../../assets/img/logo.svg";
import {menu} from "../../InfoData/menu";
import useAPI from "../../Hooks/useAPI";

import MenuItem from "../MobileMenu/MenuItem/MenuItem";
import MobileMenu from "../MobileMenu/MobileMenu";

import {StyledContainer} from "../Container/StyledContainer";
import {StyledHeader, StyledNav} from "./StyledHeader";


const Header = () => {

    const {isMobile} = useAPI();

    return (
        <StyledContainer>
            <StyledHeader>
                <NavLink to="/">
                    <img src={Logo} alt="logo"/>
                </NavLink>
                {isMobile ?
                    <MobileMenu header="menu" isFilters={false}>
                        {menu.map((item, i) =>
                            <MenuItem activeClassName="active" exact={item.exact} to={item.path} i={i} title={item.title}/>
                        )}
                    </MobileMenu>
                    :
                    <StyledNav>
                        {menu.map(item =>
                            <NavLink activeClassName={"active"} exact={item.exact} key={item.title} to={item.path}>{item.title}</NavLink>
                        )}
                    </StyledNav>
                }
            </StyledHeader>
        </StyledContainer>
    )
}


export default React.memo(Header);
