import * as React from "react";
import {useState} from "react";

import BodyItem from "./BodyItem/BodyItem";

import {StyledBodyItem} from "./BodyItem/StyledBodyItem";
import {StyledTableBody} from "./StyledTableBody";

import {ReactComponent as COIN} from "../../../../../assets/img/icons/coin.svg";
import {ReactComponent as DOG} from "../../../../../assets/img/dogs/dog-player.svg";

const TableBody = ({lobbies}) => {
    const [activeLobby, setActiveLobby] = useState(-1);

    // console.log("started active lobby-NUMBER:/n ", activeLobby);
    return (
        <StyledTableBody>
            {lobbies.map((lobby, i) => (
                <BodyItem
                    lobby={lobby}
                    lobbyLength={lobby.length()}
                    lobbyNumber={i}
                    key={lobby.id}
                    activeLobby={activeLobby === i}
                    setActiveLobby={setActiveLobby}
                />
            ))}
            {/*for balance empty table*/}
            <StyledBodyItem style={{visibility: "hidden", pointerEvents: 'none'}} className="MuiTableRow-root sc-iCfMLu hjfJyF table-item css-1q1u3t4-MuiTableRow-root" id="tableitem1">
                <th className="MuiTableCell-root MuiTableCell-body MuiTableCell-sizeMedium css-1ex1afd-MuiTableCell-root"
                    scope="row">Racing In the life artoice
                </th>
                <td className="MuiTableCell-root MuiTableCell-body MuiTableCell-sizeMedium css-1ex1afd-MuiTableCell-root">Olympic
                    stadium
                </td>
                <td className="MuiTableCell-root MuiTableCell-body MuiTableCell-sizeMedium css-1ex1afd-MuiTableCell-root">Grass</td>
                <td className="MuiTableCell-root MuiTableCell-body MuiTableCell-sizeMedium css-1ex1afd-MuiTableCell-root">
                    <span className="span">Class IV</span></td>
                <td className="MuiTableCell-root MuiTableCell-body MuiTableCell-sizeMedium css-1ex1afd-MuiTableCell-root">
                    Preparing
                </td>
                <td className="MuiTableCell-root MuiTableCell-body MuiTableCell-sizeMedium css-1ex1afd-MuiTableCell-root">
                    <COIN/>
                    100
                </td>
                <td className="MuiTableCell-root MuiTableCell-body MuiTableCell-sizeMedium css-1ex1afd-MuiTableCell-root">1
                    lap
                </td>
                <td className="MuiTableCell-root MuiTableCell-body MuiTableCell-sizeMedium players3 css-1ex1afd-MuiTableCell-root">
                    <DOG/><DOG/><DOG/><DOG/><DOG/>
                </td>
            </StyledBodyItem>
        </StyledTableBody>
    )
}

export default React.memo(TableBody);