import React, { useEffect, useMemo, useRef, useState} from "react";
import {scroller} from "react-scroll";

import {countClass} from "../../../../../../utils/countClass";
import useAPI from "../../../../../../Hooks/useAPI";

import ActiveLobby from "./Components/ActiveLobby/ActiveLobby";
import Lobby from "./Components/Lobby/Lobby";

import {StyledBodyItem} from "./StyledBodyItem";

const BodyItem = ({lobby, lobbyNumber, activeLobby, setActiveLobby, lobbyLength}) => {
    const ref = useRef();
    const {setLobby, setUser, user} = useAPI();

    const [selectingStage, setSelectingStage] = useState("dog");
    // const [lobbyHeight, setLobbyHeight] = useState(null);

    const isUser = Object.keys(user).length;
    const isAlert = useMemo(() => lobby.length() < 5 && !isUser ? 1 : 0, [isUser]);
    const lobbyInfo = useMemo(() => ({
        status: lobby.stat ? lobby.stat === 2 ? "In Process" : "Open" : "Preparing",
        classRating: countClass(lobby.clas),
        playersClass: "players p" + lobbyLength,
    }), [lobbyLength, lobby.clas, lobby.stat])

    const scroll = () => {
        const scrollTo = () => {
            scroller.scrollTo(lobby.id, {
                duration: 500,
                offset: -86,
                smooth: "easeInOut",
            })
        }
        setTimeout(scrollTo, 501);
    }

    const tabOnClick = () => {
        if (!activeLobby) {
            setActiveLobby(lobbyNumber);
            scroll();
        }
    }

    const removeSecond = () => {
        switch (lobby.timer) {
            case null:
                //start preparing
                setLobby((item) => ({...item, timer: 59}), lobby.id);
                break;
            case 52:
                //end of preparing
                setLobby((item) => ({...item, stat: 2}), lobby.id);
                // window.open('/race');
                break;
            default:
                //preparing
                setLobby((item) => ({...item, timer: lobby.timer - 1}), lobby.id);
                break;
        }
    }

    // start timer
    useEffect(() => {
        if (lobbyLength === 5) {
            if (lobby.stat === 0) {
                setTimeout(removeSecond, 1000);
            } else if (lobby.stat === 2) { //set Dog race, and remove Selected Dog
                if (lobby.id === user?.lobbyID)
                    setUser(user => ({
                        ...user,
                        selectDog: ({
                            ...user?.selectDog, inRace: true, lobbyID: lobby.id
                        }),
                        userDogs:
                            user?.userDogs.map((userDog) =>
                                userDog.id === user?.selectDog?.id ?
                                    ({
                                        ...userDog, inRace: true,
                                    }) : userDog)
                    }))
                setLobby((item) => ({...item, timer: null}), lobby.id);
            } else {
                setLobby((item) => ({...item, timer: null}), lobby.id);
            }
        }
    }, [lobby.timer, lobby.stat])

    //start preparing
    useEffect(() => {
        if (lobbyLength === 5) {
            setLobby((item) => ({...item, stat: 0}), lobby.id);
        } else {
            setLobby((item) => ({...item, stat: 1}), lobby.id);
        }
    }, [lobbyLength])

    return (
    <>
        <StyledBodyItem
            onClick={() => tabOnClick()}
            open={activeLobby}
            alert={isAlert}
            id={lobby.id}
            ref={ref}
        >
            {!activeLobby ?
                <Lobby
                    lobbyInfo={lobbyInfo}
                    lobby={lobby}
                />
                :
                <ActiveLobby
                    lobby={lobby}
                    user={user}
                    setActiveLobby={setActiveLobby}
                    lobbyInfo={lobbyInfo}
                    selectingStage={selectingStage}
                    setSelectingStage={setSelectingStage}
                    lobbyNumber={lobbyNumber}
                />
            }
        </StyledBodyItem>
        <StyledBodyItem/>
    </>
    )
}
export default React.memo(BodyItem);