import * as React from 'react';
import {useEffect, useState} from 'react';

import useUser from "../../../Hooks/useAPI";
import {lobbiesAPI} from "../../../InfoData/lobbyAPI";

import Table from '@mui/material/Table';
import TableHeader from './Components/Header/TableHeader';
import TableBody from "./Components/Body/TableBody";
import ActiveFilters from "./Components/ActiveFilters/ActiveFilters";

import {StyledTableContainer} from "./StyledTable";

const TableLobbies = () => {

    const {lobbies, setLobbies, isMobile} = useUser();
    const [showFilters, setShowFilters] = useState(false);
    const [filterOptions, setFilterOptions] = useState({
        index: null, filterKey: [], name: null
    });

    const sortOptionsArray = () => {
        const array = [];
        filterOptions.filterKey.map((item) => {
            if (array[item[1]]) {
                array[item[1]].push(item[0]);
            } else {
                array[item[1]] = [item[0]]
            }
        })
        return array;
    }

    const filterRow = (row) => {
        const rowMassive = Object.values(row);
        const flex = sortOptionsArray()
        if(filterOptions.filterKey.length) {
            return flex
                .map((filter, column) => filter
                    .map(key => rowMassive[column]?.toString().includes(key))
                    .some(item => Boolean(item)))
                .filter((item) => item !== undefined)
                .every(item => Boolean(item));
        }
        else return  row;
    }

    useEffect(()=> {
        setLobbies(lobbiesAPI);
    }, [lobbiesAPI])

    useEffect(() => {
        setLobbies(lobbiesAPI.filter(row => filterRow(row)))
    },[filterOptions])

    return (
        <>
            {isMobile ? null : <ActiveFilters isMobile={isMobile} filterOptions={filterOptions} setFilterOptions={setFilterOptions}/>}
            <StyledTableContainer className={showFilters ? "showFilters" : ""}>
                <Table>

                    <TableHeader filterOptions={filterOptions} setFilterOptions={setFilterOptions} showFilters={showFilters} setShowFilters={setShowFilters}/>

                    {isMobile ? <ActiveFilters isMobile={isMobile} filterOptions={filterOptions} setFilterOptions={setFilterOptions}/> : null}

                    <TableBody lobbies={lobbies} setLobbies={setLobbies}/>
                    
                </Table>
            </StyledTableContainer>
        </>);
}


export default React.memo(TableLobbies);