import React from "react";

import {StyledBanner} from "./StyledBanner";

import runDog from '../../../assets/img/dogs/runDog.svg'
import circle from '../../../assets/img/banner/banner-circle.svg'
import square from '../../../assets/img/banner/banner-square.svg'
import cross from '../../../assets/img/banner/banner-cross.svg'

const MainBanner = () => {
    return (
        <StyledBanner>
            <div className="banner__text-container">
                <h1>Lobby list</h1>
                <p>To take part in the race - please select the game you are interested in, click on it and select the dog.
                    Good luck!</p>
            </div>
            <div className="banner__svg-container">
                <img className="circle" src={circle} alt="squid-game-circle-art" />
                <img className="square" src={square} alt="squid-game-square-art" />
                <img className="cross" src={cross} alt="squid-game-cross-art" />
            </div>
            <img src={runDog} alt="running-dog-nft" className="running-dog"/>
        </StyledBanner>
    );
}

export default React.memo(MainBanner);