import React, {useEffect, useRef, useState} from "react";
import {Route, useLocation} from "react-router-dom";
import Lobby from "../Tabs/Lobby";
import Roadmap from "../Tabs/Roadmap";
import FAQ from "../Tabs/FAQ";
import Wallet from "../Tabs/Wallet";
import Race from "../Tabs/Race";
import AuthPopUp from "../uiComponents/AuthPopUp/AuthPopUp";
import useAPI from "../Hooks/useAPI";
import Spinner from "../uiComponents/Spinner/spinner";

const Routes = () => {
    const location = useLocation();

    const auth = useRef();
    const {user, setUser} = useAPI();

    const [shouldShowPopUp, setShouldShowPopUp] = useState(true);
    const [isOpenPopUp, setIsOpenPopUp] = useState(false);
    const [spinner, setSpinner] = useState(false);

    const closePopUp = () => {
        document.getElementById("popUp136")?.classList.add("fadeOut");
        const closingHandler = () => {
            setIsOpenPopUp(false)
            setShouldShowPopUp(false)
        }
        setTimeout(closingHandler, 501);
    }

    const popUp = shouldShowPopUp && isOpenPopUp;

    useEffect(()=> {
        !user?.length && !location.pathname.includes('wallet') ? setIsOpenPopUp(true) : null;
    }, [user?.length, location.pathname])

    return (
        <>
            {popUp ?
                <div ref={auth}>
                    <AuthPopUp id="popUp136" className="fadeIn" closePopUp={closePopUp} setUser={setUser} setSpinner={setSpinner}/>
                </div> : null
            }
            {spinner && <Spinner />}
            <Route exact path="/">
                <Lobby/>
            </Route>
            <Route path="/wallet">
                <Wallet setShowPopUp={closePopUp}/>
            </Route>
            <Route path="/roadmap">
                <Roadmap/>
            </Route>
            <Route path="/faq">
                <FAQ/>
            </Route>
            <Route path="/race">
                <Race/>
            </Route>
        </>
    )
}

export default React.memo(Routes);
