import React, {useEffect} from "react";
import { io } from 'socket.io-client';
import MainBanner from "../Sections/Lobby/MainBanner/MainBanner";
import TableLobbies from "../Sections/Lobby/TableLobbies/TableLobbies";

import {StyledContainer} from "../uiComponents/Container/StyledContainer";

const config = {
    url: "http://localhost:3001"
}

const Lobby = () => {

    useEffect(()=> {
        const socket = io(config.url, {
            transports: ['websocket', 'polling', 'flashsocket']
        });

        socket.on('connect', () => console.log('socket connect'));

        socket.on('disconnect', (reason) => {
            if (reason === 'io server disconnect') {
                socket.connect();
            }
        });

        socket.on('new-lobby', (lobby) => {
            console.log('new-lobby', lobby);
        });
        socket.on('update-lobby', (lobby) => {
            console.log('update-lobby', lobby);
        });

        return () => {
            if (socket) {
              socket.disconnect();
            }
        };

    }, []);

    return (
        <StyledContainer>

            <MainBanner/>

            <TableLobbies/>

        </StyledContainer>
    )
}
export default React.memo(Lobby);