import React, {useEffect} from 'react';
import useUser from "../../../../../../../../Hooks/useAPI";
import DogCard from "../../../../../../../../uiComponents/DogCard/DogCard";

import {StyledSelectDog} from "./StyledSelectDog";
import {ColoredScrollbars} from "../../../../../../../../uiComponents/CustomScrollBar/CustomScrollBar";
import PrimaryButton from "../../../../../../../../uiComponents/Buttons/Primary/PrimaryButton";

const SelectDog = ({lobby, setSelectingStage}) => {
    const {user, setUser, lobbies} = useUser();

    const classFitDog =
        user?.userDogs?.filter((dog) => (dog.class === lobby.clas));

    const buttonContent =
        !classFitDog?.length ? "Not suitable dogs for the selected race" : "Confirm select";

    const CanYouSelectDog =
        !classFitDog?.length || user?.selectDog === -1 ||
        lobby.clas !== user?.selectDog?.class || user?.selectDog?.inRace;

    const selectDog = (dog) => {
        setUser({...user, selectDog: dog})
    }

    //exit select dog if no user
    useEffect(() => {
        const isYourAnotherLobbyPreparing =
            lobby.id !== user?.lobbyID
            && lobbies.find(item => item.id === user?.lobbyID)?.stat === 0;

        if(!Object.keys(user).length || isYourAnotherLobbyPreparing) setSelectingStage('place');
    },[user])

    return (
        <StyledSelectDog open={classFitDog?.length}>
            <ColoredScrollbars className="dogs-wrapper ">
                {classFitDog?.length ?
                    classFitDog.map((dog, i) => (
                        <DogCard
                            secondaryState={lobby.clas !== dog.class}
                            active={user.selectDog === dog}
                            dog={dog}
                            key={i}
                            selectDog={() => selectDog(dog)}
                        />
                    ))
                    : ""
                }
            </ColoredScrollbars>
            <PrimaryButton
                className="confirm"
                content={buttonContent}
                isDisabled={CanYouSelectDog}
                onClick={() => setSelectingStage("place")}
            />
        </StyledSelectDog>
    );
};

export default React.memo(SelectDog);