export const countClass = (classRate) => {
    switch (classRate) {
        case 0:
            return "Beginner";
        case 1:
            return "Class I";
        case 2:
            return "Class II";
        case 3:
            return "Class III";
        case 4:
            return "Class IV";
        case 5:
            return "Class V";

    }
}