import styled from "styled-components";
import {baseTheme} from "../../../../../styles/baseTheme";

export const StyledActiveFilters = styled.div`
  font-family: ${baseTheme.fonts.primary};
  color: #fff;
  display: flex;
  align-items: flex-start;
  overflow: hidden;
  transition: 0.3s;
  height: ${props => props.active};
  
  h3 {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 23px;
    text-transform: capitalize;
    color: #FFFFFF;
    position: relative;
    margin-right: 46px;
    padding-top: 3px;
    min-width: 68px;
    &::after {
      content: "";
      width: 0.7px;
      height: 14px;
      background-color: #fff;
      right: -24px;
      position: absolute;
      transform: translateY(-50%);
      top: 50%;
    }
  }
  .active-filters-container {
    
  }
  @media ${baseTheme.media.mobile} {
    display: flex;
    flex-direction: column;
    h3 {
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 34px;
      text-transform: capitalize;
      margin-bottom: 6px;
      &::after {
        content: none;
      }
    }
    .active-filters-container {
      height: ${props => props.height + "px"} !important;
      width: 100%;
      .box {
        font-family: ${baseTheme.fonts.primary};
        color: #fff;
        display: flex;
        align-items: flex-start;
        overflow: hidden;
        transition: 0.3s;
        height: ${props => props.active};
        overflow-x: scroll;
        overflow-y: visible;
        &::-webkit-scrollbar {
          display: none;
        }

        /* Hide scrollbar for IE, Edge and Firefox *
          -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
      }

      .vthumb {
        background-color: ${baseTheme.colors.secondary};
      }

      .vtrack {
        background-color: ${baseTheme.colors.secondary};
      }
      
      .track {
        bottom: 49px !important;
        opacity: 0.5 ;
      }
    }
  }

`