import React, {useState} from 'react';
import { Scrollbars } from 'react-custom-scrollbars-2';
import {baseTheme} from "../../styles/baseTheme";

export const ColoredScrollbars =(props)=> {

    const [state,setState] = useState({left:0});

    const handleUpdate = (values) => {
        const { left } = values;
        setState({ left,  });
    }

    const renderView = ({ style, ...props }) => {
        // const { left } = state;
        const viewStyle = {
            padding: "2px 0 0",
            overflowY: "hidden",
            // backgroundColor: `rgb(${Math.round(255 - (top * 255))}, ${Math.round(top * 255)}, ${Math.round(255)})`
        };
        return (
            <div
                className="box"
                style={{ ...style, ...viewStyle }}
                {...props}/>
        );
    }

    const renderThumb = ({ style, ...props }) => {
        const { left } = state;
        const thumbStyle = {
            backgroundColor: baseTheme.colors.secondary,
            left: (left * 100)/3  + "%",
            width: 200/3 + "%",
            borderRadius: "20px",
            boxShadow: "0px 3px 3px -1px inset rgba(255, 255, 255, 30%)"
        };
        return (
            <div
                className="thumb"
                style={{ ...style, ...thumbStyle }}
                {...props}/>
        );
    }

    const renderTrack = ({ style, ...props }) => {
        // const { left } = state;
        const trackStyle = {
            display: "block",
            width:  "100%",
            bottom: "-13px"
        };
        return (
            <div
                className="track"
                style={{ ...style, ...trackStyle }}
                {...props}/>
        );
    }

    return (
        <Scrollbars
            renderView={renderView}
            renderThumbHorizontal={renderThumb}
            renderTrackHorizontal={renderTrack}
            onUpdate={handleUpdate}
            {...props}
            autoHide={true}
        >{props.children}</Scrollbars>
    );
}