import React from 'react';
import PrimaryButton from "../Buttons/Primary/PrimaryButton";
import {userAPI} from "../../InfoData/userAPI";
import Svg from "../../utils/svg";

import {StyledAuthPopUp} from "./StyledAuthPopUp";

import {ReactComponent as Exit} from "../../assets/img/icons/exit.svg";

const AuthPopUp = ({closePopUp, setUser, id, className, setSpinner}) => {

    const connectWallet = () => {
        setSpinner(true);
        closePopUp();
        setTimeout(() => {
            setUser(userAPI);
            setSpinner(false);
        }, 3000);
    }

    return (
        <StyledAuthPopUp id={id} className={className}>
            <div className="overlay" onClick={()=>closePopUp()}/>
            <div className="popup">
                {/*<h2>Log-in</h2>*/}
                <Svg component={<Exit className="exit" onClick={()=>closePopUp()}/>}/>
                <PrimaryButton content={"Guest"} onClick={()=>closePopUp()}/>
                <PrimaryButton content={"Connect Wallet"} onClick={()=>connectWallet()}/>
            </div>
        </StyledAuthPopUp>
    );
};

export default AuthPopUp;
