import styled from "styled-components";
import TableRow from "@mui/material/TableRow";
import {baseTheme} from "../../../../../../styles/baseTheme";


export const StyledBodyItem = styled(TableRow)`
  && {
    scroll-margin: 83px;
    background-color: ${baseTheme.colors.primary};
    transition: 0.5s ease-in-out;
    height: ${(props) => props.open ? props.alert ? "552px" : "516px" : '43px'};
    cursor: ${(props) => props.open ? "initial" : "pointer"};
    vertical-align: ${(props) => props.open ? "top" : "middle"};
    ${baseTheme.keyframes.fadeIn(0, 0.3)};

    &:last-child * {
      pointer-events: none;
    }

    &:nth-of-type(2n) {
      height: 4px;
      background-color: transparent;
      //border-top: 1.5px solid #1D1D46;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        top: 0;
        height: 1.5px;
        width: 100%;
        background-color: #1D1D46;
      }
    }

    &:hover th {
      color: ${baseTheme.colors.secondary};
    }

    th, td {
      padding: 0 20px;
      height: 42px;
      color: #fff;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 20px;
      position: relative;
      border-bottom: none;
    }

    th {
      letter-spacing: 0.02em;
      text-transform: uppercase;
      font-family: ${baseTheme.fonts.primary};
    }

    td {
      height: 100%;
      font-family: ${baseTheme.fonts.primary};

      &:nth-of-type(1), &:nth-of-type(2) {
        letter-spacing: 0.03em;
        text-transform: capitalize;
        color: #9898B0;
      }

      &:nth-of-type(3) {
        letter-spacing: 0.05em;
        text-transform: capitalize;
        color: #FFFFFF;

        span {
          ${baseTheme.mixins.classButton("14px")}
        }
      }

      &:nth-of-type(4) {
        font-weight: 600;
        font-size: 12px;
        line-height: 16px;
        font-family: ${baseTheme.fonts.secondary};
        color: #C2A922;

        &.prepare {
          color: #C93685;
        }

        &.open {
          color: #2BA866;
        }
      }

      &:nth-of-type(5) {
        height: 43px;
        font-weight: 600;
        color: #FCC417;
        display: table-cell;

        svg {
          margin-right: 4px;
          position: relative;
          top: 2px;
        }
      }

      &:nth-of-type(6) {
        font-family: ${baseTheme.fonts.secondary};
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.03em;
        text-transform: capitalize;
        color: #9898B0;
      }

      &:nth-of-type(7) {
        vertical-align: middle;

        svg {
          margin-top: 8px;
          margin-right: 7px;
        }

        &.players {
          &.p1 svg:nth-of-type(1) path,
          &.p2 svg:nth-of-type(-n+2) path,
          &.p3 svg:nth-of-type(-n+3) path,
          &.p4 svg:nth-of-type(-n+4) path,
          &.p5 svg path {
            stroke: none;
            fill: #2029D6;
          }
        }
      }
    }
  }

  @media ${baseTheme.media.mobile} {
    &&:nth-of-type(2n) {
      height: 4px;
      background-color: transparent;
      padding: 0;
    }

    &&:nth-of-type(2n + 1) {
      border-bottom: 1px solid #1D1D46;
    }

    && {
      display: flex;
      flex-wrap: wrap;
      height: initial;
      padding: ${(props) => props.open ? "0" : "14px"};
      transition: height 0.5s, min-height 0.5s ease-in-out;


      position: relative;

      th, td {
        border: none;
        width: 100%;
        display: inline-block;
        width: initial;
        height: initial;
        padding: 0;
        position: relative;

        &::after {
          content: none;
        }
      }

      th {
        width: 100%;
        margin-bottom: 5px;
        order: 0;
        font-size: 20px;
      }

      td {
        &:nth-of-type(1),
        &:nth-of-type(2),
        &:nth-of-type(3) {
          font-size: 16px;
        }

        &:nth-of-type(1),
        &:nth-of-type(2) {
          margin-right: 12px;
          position: relative;

          &::after {
            content: "";
            width: 4px;
            height: 4px;
            background-color: #9898B0;
            border-radius: 20px;
            position: absolute;
            right: -8px;
            top: 50%;
            transform: translateY(-50%);
            margin-left: calc(100% + 4px);
          }
        }

        &:nth-of-type(1) {
          order: 3;
        }

        &:nth-of-type(2) {
          order: 4;
        }

        &:nth-of-type(3) {
          order: 6;
          width: 100%;
          margin-top: 14px;

          span {
            font-size: 16px;
          }
        }

        &:nth-of-type(4) {
          order: 2;
          width: calc(100% - 125px);
          padding-top: 10px;
          padding-left: 13px;
          box-sizing: border-box;
          font-size: 14px;
        }

        &:nth-of-type(5) {
          position: absolute;
          right: 14px;
          top: 14px;

          svg {
            top: 0.8px;
            height: 11px;
          }
        }

        &:nth-of-type(6) {
          order: 5;
          font-family: ${baseTheme.fonts.primary};
        }

        &:nth-of-type(7) {
          order: 1;
          margin-bottom: 8px;
          width: 125px;
        }
      }
    }
  }
}

@keyframes heightIn {
  from {
    height: 0;
  }
  to {
    height: 588px;
  }
}
`