import React from "react";

import AnimateHeight from "react-animate-height";
import {ColoredScrollbars} from "../../../../../uiComponents/CustomScrollBar/CustomScrollBar";
import {StyledActiveFilters} from "./StyledActiveFilters";

import {ReactComponent as Exit} from "../../../../../assets/img/icons/exit.svg"
import PrimaryButton from "../../../../../uiComponents/Buttons/Primary/PrimaryButton";

const ActiveFilters = ({filterOptions, setFilterOptions, isMobile}) => {
    const deleteFilter = (indexDelete) => {
        // delete activeFilter
        setFilterOptions({
            ...filterOptions,
            filterKey: filterOptions.filterKey.filter((item, i) => i !== indexDelete),
        })
    }

    const height = filterOptions.filterKey.length ? isMobile ? 110 : "auto" : 0
    return (
        <AnimateHeight height={height}>
            <StyledActiveFilters height={height}>
                {/*active={filterOptions.filterKey.length ? filterOptions.filterKey.length > 10 ? "90px" : "49px" : 0}>*/}
                <h3>Active Filters</h3>
                <div className={"active-filters-container"}>
                    {isMobile ?
                        <ColoredScrollbars>
                            {filterOptions.filterKey.map((item, i) => (
                                <PrimaryButton key={i} className={"active-filter"} onClick={() => deleteFilter(i)} content={<>{item[2]}<Exit/></>} />
                            ))}
                        </ColoredScrollbars>
                        :
                        filterOptions.filterKey.map((item, i) => (
                            <PrimaryButton key={i} className="active-filter" onClick={() => deleteFilter(i)} content={<>{item[2]}<Exit/></>} />
                        ))
                    }
                    <button className={"active-filter"} style={{opacity: 0}}>flex<Exit/></button>
                </div>
            </StyledActiveFilters>
        </AnimateHeight>
    )
}

export default React.memo(ActiveFilters);