import { createGlobalStyle } from 'styled-components'
import {baseTheme} from "./baseTheme";
import BG from "../assets/img/bg.svg";

export default createGlobalStyle`
  #root {
    position: relative;
    //min-height: 109vh;
    min-height: 100vh;
    overflow-x: hidden;
    &.overflow {
      height: 100vh;
      overflow: hidden;
    }
  }
  body {
    margin: 0;
    background-color: ${baseTheme.colors.bg};
    background-image: url(${BG});
    background-size: contain;
    background-repeat: no-repeat;
    background-position-y: 150px;
  }
  a {
    color: #fff;
    text-decoration: none;
    &:hover{
      color:#fff
    }
  }
  h1,h2,h3,h4,h5,h6,p {
    margin: 0;
  }
  .fadeIn {
    animation: fadeIn 0.5s ease-in-out forwards;
  }
  .fadeOut {
    animation: fadeOut 0.5s ease-in-out forwards;
  }

  @-webkit-keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @-webkit-keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  @keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  #root {
    .slideInLeft-appear,
    .slideInLeft-enter {
      transform: translateX(100%);
      opacity: 0;
    }
    .slideInLeft-enter-active, .slideInLeft-enter-done, .slideInLeft-appear-active, .slideInLeft-appear-done {
      transform: translateX(0%);
      opacity: 1;
      transition-duration: 0.4s;
    }
    .slideInLeft-exit {
      transform: translateX(0%);
      margin-left: 0;
      opacity: 1;
    }
    .slideInLeft-exit-active, .slideInLeft-exit-done {
      transform: translateX(100%);
      opacity: 0;
      transition-duration:  0.4s;
    }
    .slideInLeft-enter-active, .slideInLeft-appear-active, .slideInLeft-exit-active {
      will-change: transform;
    }
  }
  
`