import React, {useEffect, useState} from 'react';

import PrimaryButton from "../../../../../../../../uiComponents/Buttons/Primary/PrimaryButton";
import SelectPlace from "../SelectPlace/SelectPlace";
import Svg from "../../../../../../../../utils/svg";
import SelectDog from "../SelectDog/SelectDog";

import {StyledActiveLobby} from "./StyledActiveLobby";

import OLmap from "../../../../../../../../assets/img/maps/OlympicStadium.jpg";
import {ReactComponent as Exit} from "../../../../../../../../assets/img/icons/exit.svg";
import {ReactComponent as COIN} from "../../../../../../../../assets/img/icons/coin.svg";
import {ReactComponent as Info} from "../../../../../../../../assets/img/icons/info.svg";
import {ReactComponent as Timer} from "../../../../../../../../assets/img/icons/timer.svg";
import {ReactComponent as DOG} from "../../../../../../../../assets/img/dogs/dog-player.svg";
import {ReactComponent as ArrRight} from "../../../../../../../../assets/img/icons/ArrRight.svg";
import AnimateHeight from "react-animate-height";

const ActiveLobby = ({lobby, user, setActiveLobby, lobbyInfo, lobbyNumber, selectingStage, setSelectingStage}) => {

    const [height, setHeight] = useState(0);


    const willClose = () => {
        const animationLobby = document.getElementById(lobby.id);
        const {clientHeight} = animationLobby.children[0];
        animationLobby.style.minHeight = clientHeight + "px";
    }

    const close = () => document.getElementById(lobby.id).style.minHeight = "0px";

    useEffect(() => {
        setHeight('auto');
        return () => close();
    })

    return (
        <StyledActiveLobby colSpan={8}>
            <AnimateHeight height={height} duration={500} easing="ease-in-out" contentClassName="lobby__wrapper" onAnimationEnd={()=> willClose()}>
                <button className="close-active" onClick={() => setActiveLobby(-1)}>Close <Exit/></button>
                <h2>{lobby.name} </h2>
                {lobby.stat === 2 ? <PrimaryButton content="Spectate" className="spectate"/> : ""}
                <div className="lil-info">
                    <span className="coin"><COIN/> {lobby.amountCoin}</span>
                    <span className='status'>
                                    <Info/> Status:
                                    <bdi
                                        className={"status-v" + (!lobby.stat ? " prepare" : lobby.stat === 1 ? " open" : "")}
                                    >{lobbyInfo.status}
                                    </bdi>
                                </span>
                    <span className={"timer" + (lobby.timer ? " started" : "")}>
                                    <Timer/> Starts in: <bdi>00:{lobby.timer > 9 ? lobby.timer : "0" + lobby.timer}</bdi>
                                </span>
                </div>
                <h3 className="lil-header">Details</h3>
                <span className="location">Location: <bdi>{lobby.loc}</bdi></span>
                <span className="evtType">Event Type: <bdi>{lobbyInfo.classRating}</bdi></span>
                <span className="laps">Laps: <bdi>{lobby.laps}</bdi></span>
                <span className={lobbyInfo.playersClass}>Players: <DOG/><DOG/><DOG/><DOG/><DOG/></span>
                <h3 className="lil-header">Participation</h3>
                <div className="selecting">
                    <span className={'select-dog' + (selectingStage === 'dog' ? " active" : "")}>Select dog </span> <Svg
                    component={<ArrRight className="arrRight"/>}/>
                    <span className={'select-place' + (selectingStage !== 'dog' ? " active" : "")}>Select Place</span>
                    {selectingStage === 'dog' && !user?.selectDog?.inRace ? //selecting
                        <SelectDog
                            lobby={lobby}
                            setSelectingStage={setSelectingStage}
                        />
                        :
                        <SelectPlace
                            setSelectingStage={setSelectingStage}
                            lobbyNumber={lobbyNumber}
                            lobby={lobby}
                        />
                    }
                </div>
                <img src={OLmap} className="map" alt="track-map"/>
            </AnimateHeight>
        </StyledActiveLobby>
    );
};

export default React.memo(ActiveLobby);