import React, { useMemo, useState} from "react";

import {TableCell} from "@mui/material";
import TableRow from "@mui/material/TableRow";
import useAPI from "../../../../../Hooks/useAPI";
import HeaderItem from "./HeaderItem/HeaderItem";
import {headers, filtersLIB} from "../../../../../InfoData/tableFilters";
import FilterSelect from "../../../../../uiComponents/FilterSelect/FilterSelect";
import MobileFilters from "../../../../../uiComponents/MobileFilters/MobileFilters";

import {StyledTableHead} from "./StyledTableHeader";

const TableHeader = ({filterOptions, setFilterOptions, showFilters, setShowFilters}) => {

    const {isMobile} = useAPI();

    const [activeColumnFilter, setActiveColumnFilter] = useState(null);
    const [activeFilterItems, setActiveFilterItems] = useState([]);

    const handleFilters = (item, i) => {
        if (activeColumnFilter === i) {
            setShowFilters(!showFilters);
            if (showFilters && filterOptions.index !== activeColumnFilter) {
                setActiveColumnFilter(null);
            }
        } else {
            setFilterOptions({...filterOptions, name: item});
            setShowFilters(true);

            setActiveFilterItems(filtersLIB[i]);
            setActiveColumnFilter(i);
        }
    }

    const checkActiveVariantFilter = (item, filterColumn) => {

        const compare = () => {
            return !!filterOptions.filterKey.some(filterKey => {
                return filterKey[0] === item.filterKey && filterKey[1] === filterColumn;
            });
        }

        return compare() ? " active" : "";
    }

    const handleFilterVariants = (item, filterColumn) => {
        let newKeys;
        const handling = (options, setOptions) => {
            options.filterKey.some((keyPass, i) => {
                if (keyPass[0] === item.filterKey && keyPass[1] === filterColumn) {
                    newKeys = options.filterKey.filter((item, prevI) => prevI !== i);
                    return true;
                }
            }) ?
                // delete activeFilter
                setOptions({
                    ...options,
                    filterKey: newKeys,
                })
                :
                //add activeFilter
                setOptions({
                    ...options,
                    filterKey: [...options.filterKey, [item.filterKey, filterColumn, item.name]],
                })
        }

        handling(filterOptions, setFilterOptions);
    }

    const resetFilter = (isALL) => {
        let newKeys = isALL ? [] : filterOptions.filterKey.filter(item => item[1] !== activeColumnFilter);
        setFilterOptions({
            ...filterOptions,
            filterKey:  newKeys,
        })
    }

    const setInitialOptions = (initialOptions) => setFilterOptions(initialOptions);

    const filterSelectData = useMemo(()=>({
        showFilters,
        filterOptions,
        activeFilterItems,
        activeColumnFilter,
        resetFilter,
        handleFilters,
        setShowFilters,
        checkActiveVariantFilter,
        handleFilterVariants,
        setInitialOptions,
    }), [filterOptions, activeFilterItems, activeColumnFilter, showFilters]);

    return (
        isMobile ?
            <StyledTableHead className="headers">
                <TableRow>
                    <TableCell>
                        <MobileFilters filterSelectData={filterSelectData}/>
                    </TableCell>
                </TableRow>
                <TableRow className="space"/>
            </StyledTableHead>
            :
            <>
                <StyledTableHead className="headers">
                    <TableRow>
                        {headers.map((item, i) => (
                            <HeaderItem className={activeColumnFilter === i && showFilters ? "active" : ""} onClick={() => handleFilters(item, i)} item={item} key={item} i={i}/>
                        ))}
                    </TableRow>
                    <TableRow className="space"/>
                </StyledTableHead>
                <FilterSelect filterSelectData={filterSelectData}/>
            </>
    )
}

export default React.memo(TableHeader);