import React, {useCallback, useEffect} from 'react';

import useUser from "../../../../../../../../Hooks/useAPI";
import selectDog from "../SelectDog/SelectDog";
import PlaceItem from "./PlaceItem/PlaceItem";

import {StyledSelectPlace} from "./StyledSelectPlace";
import Svg from "../../../../../../../../utils/svg";

import {ReactComponent as Alert} from "../../../../../../../../assets/img/dogs/alert.svg";
import {ReactComponent as Empty} from "../../../../../../../../assets/img/dogs/emptyDog.svg";
import PrimaryButton from "../../../../../../../../uiComponents/Buttons/Primary/PrimaryButton";
import {ColoredScrollbars} from "../../../../../../../../uiComponents/CustomScrollBar/CustomScrollBar";

const SelectPlace = ({setSelectingStage, lobby}) => {
    const {user, setUser, lobbies, setLobby} = useUser()

    const isLobbyInRace = lobby.stat !== 2;
    const isUser = Object.keys(user).length;
    const isSelectDogInRace = user?.selectDog?.inRace;
    const isAlert = lobby.length() < 5 && !isUser ? 1 : 0;
    const isYourLobbyPreparing =
        lobby.id !== user?.lobbyID
        && lobbies.find(item => item.id === user?.lobbyID)?.stat === 0;

    const placeData = useCallback((player, i) => ({
        class:
            "player-place" + (player?.id ? player.id === user?.id ? " your" : " busy" : "") + (lobby.stat === 2 ? " race" : ""),
        position:
            <p className="place-position">
                0{i + 1} PLACE {player?.id === user?.id && lobby.stat === 2 ? <span>YOUR DOG</span> : ""}
            </p>,
        nft:
            player?.id ? player.selectDog?.nft : <Svg component={<Empty/>}/>,
        name:
            <h3>{player?.id ? player.selectDog?.name : "EMPTY"}</h3>,
        state:
            <p className="play-status">
                {
                    player?.id ?
                        isLobbyInRace ?
                            player.id === user?.id ?
                                <PrimaryButton
                                    content="Change"
                                    className="change-dog"
                                    onClick={() => goChangeDog()}
                                />
                                : "Busy"
                            : "In race"
                        : isUser ?
                            isSelectDogInRace || isYourLobbyPreparing ?
                                "You are in Race"
                                : <PrimaryButton
                                    content={"Join"}
                                    className="join-place"
                                    onClick={() => takePlace(i)}
                                  />
                            :"Empty"
                }
            </p>
    }),[lobby])

    const goChangeDog = () => {
        setSelectingStage('dog');
    }

    const takePlace = (i) => {
        if(user?.lobbyID !== lobby.id && user?.lobbyID) {
            leavePlace(user?.lobbyID);
        }
        setLobby((item) => ({
            ...item, users: item.users.map((player, place) => player.id === user?.id ? {} : place === i ? user : player)
        }), lobby.id);
        setUser({...user, lobbyID: lobby.id});
    }

    const leavePlace = (lobbyID = lobby.id) => {
        setLobby((item) => ({
            ...item, users: item.users.map(player => player.id === user?.id ? {} : player)
        }), lobbyID);

        setUser(user => ({
            ...user, userDogs: user.userDogs
                .map(dog => dog === selectDog ? ({
                        ...dog, lobbyID: null
                    })
                    : dog )
        }))
    }

    // if u change dog it re-renders
    useEffect(() => {
            setLobby((item) => ({
                ...item, users:  item.users.map((player) => player ? player.id === user?.id ? user : player : {})
            }), lobby.id)},
        [user?.selectDog])

    return (
        <>
            <StyledSelectPlace>
                <ColoredScrollbars className="place-wrapper ">
                {
                    lobby.users.map((player, i) => (
                        <PlaceItem
                            placeData={placeData(player,i)}
                            key={i}
                        />
                    ))
                }
                </ColoredScrollbars>
            </StyledSelectPlace>
            {/*Check if no user*/}
            {isAlert ?
                <h3 className="alert">
                    <Svg component={<Alert/>}/>
                    Your wallet isn’t connected. Connect to join this race.
                </h3>
                : ""
            }
        </>
    );
};

export default React.memo(SelectPlace);