import React from 'react';

import TableCell from "@mui/material/TableCell";

import {ReactComponent as COIN} from "../../../../../../../../assets/img/icons/coin.svg";
import {ReactComponent as DOG} from "../../../../../../../../assets/img/dogs/dog-player.svg";

const Lobby = ({lobby,lobbyInfo}) => {
    return (
        <>
            <TableCell component="th" scope="row">{lobby.name}</TableCell>
            <TableCell>{lobby.loc}</TableCell>
            <TableCell>{lobby.type}</TableCell>
            <TableCell>
                <span className="span">{lobbyInfo.classRating}</span>
            </TableCell>
            <TableCell
                className={!lobby.stat ? "prepare" : lobby.stat === 1 ? "open" : ""}
            >
                {lobbyInfo.status}
            </TableCell>
            <TableCell>
                <COIN/>{lobby.amountCoin}
            </TableCell>
            <TableCell>
                {lobby.laps} {lobby.laps === 1 ? "lap" : "laps"}
            </TableCell>
            <TableCell
                className={lobbyInfo.playersClass}
            >
                <DOG/><DOG/><DOG/><DOG/><DOG/>
            </TableCell>
        </>
    );
};

export default React.memo(Lobby);