import React, {useState} from 'react';

import PrimaryButton from "../../../uiComponents/Buttons/Primary/PrimaryButton";
import {userAPI} from "../../../InfoData/userAPI";

import {StyledTitleWallet, StyledWalletNoUser} from "./StyledTitleWallet";
import Spinner from "../../../uiComponents/Spinner/spinner";

const TitleWallet = ({user, setUser, setShowPopUp}) => {

    const [spinner, setSpinner] = useState(false);

    const connectWallet = () => {
        setSpinner(true);
        setTimeout(() => {
            setSpinner(false);
            setUser(userAPI);
            setShowPopUp(false);
        }, 3000);
    }
    return (
        user ?
                <StyledTitleWallet>
                    <h1 className="title">YOUR <span>DOGS</span></h1>
                    <p>Here you can view all your dogs and their statistics.</p>
                </StyledTitleWallet>
            :
            <>
                {spinner ? <Spinner /> : <StyledWalletNoUser>
                    <h1 className="title">Wallet isn’t connected</h1>
                    <p>To have access to your purchases - connect your wallet. After connecting, all your dogs/ tracks will be displayed here.</p>
                    <PrimaryButton content="Connect Wallet" onClick={()=>connectWallet()}/>
                </StyledWalletNoUser>}
            </>

    );
};

export default React.memo( TitleWallet );
