import React from 'react';
import {sorting} from "../../../../InfoData/walletFilters";
import {ReactComponent as ArrDown} from "../../../../assets/img/icons/ArrDown.svg";

const Sorting = ({showSorting, setShowSorting, filters, handleSorting}) => {
    console.log(filters)
    return (
        <div className={"right" + (showSorting ? " active" : "")}>
            <button className="sorting"
                    onClick={() => setShowSorting(!showSorting)}
            >{filters?.sorting !== -1 ? sorting[filters?.sorting] : "Sort by"}
                <ArrDown/>
            </button>
            <ul className="variants">
                {sorting.map((item, i) => (
                    <li key={item} onClick={() => handleSorting(i)}
                        className={i === filters?.sorting ? " active" : ""}>{item}</li>
                ))}
            </ul>
        </div>
    );
};

export default Sorting;