import styled from "styled-components";
import {baseTheme} from "../../../../../../../../styles/baseTheme";

export const StyledSelectDog = styled.div`

  animation: fadeIn 0.5s ease-in-out forwards ;
  .dogs-wrapper {
    // height: ${(props) => props.open ? "130px !important" : "70px !important" };
    overflow: visible !important;
    .dogs-container {
      //overflow-x: scroll;
      display: flex;
    }
  }
  .box {
    display: flex;
    //height: 130px;
    overflow-x: scroll;
    overflow-y: initial;
    &::-webkit-scrollbar {
      display: none;
    }

    /* Hide scrollbar for IE, Edge and Firefox *
      -ms-overflow-style: none;  /* IE and Edge */
      scrollbar-width: none;  /* Firefox */
  }

  .vthumb {
    background-color: ${baseTheme.colors.secondary};
  }

  .vtrack {
    background-color: ${baseTheme.colors.secondary};
  }

  .confirm {
    margin-top: 24px;
  }
  .dogs-wrapper {
    height: ${(props) => props.open ? "128px !important" : "70px !important" };
  }
  @media ${baseTheme.media.mobile} {
    .dogs-wrapper {
      height: ${(props) => props.open ? "160px !important" : "70px !important" };
    }
  }
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }
`