import React, {useEffect, useState} from 'react';
import DogCard from "../../../uiComponents/DogCard/DogCard";
import {sortDogs} from "../../../utils/sortDogs";

import {StyledYourDogs} from "./StyledYourDogs";

const YourDogs = ({className, filters, user, setUser}) => {

    const [data, setData] = useState(user?.userDogs);

    const handleFilters = (dog) => {
        const winRated = dog.winRate > filters.winRate[0] && dog.winRate < filters.winRate[1];
        const filtered = filters.clas.length ? filters.clas.some( filter => dog.class === filter.filterKey ) : true;
        const searched = filters.search ? dog.name.toLowerCase().includes(filters.search) : true;
        return winRated && filtered && searched;
    }

    //sorting
    useEffect(()=> {
        setData(currentUser => sortDogs(currentUser, filters))
    }, [filters.sorting])


    return (
        <StyledYourDogs className={className} isWallet={true}>
            {data.filter((dog) => handleFilters(dog)).map(dog => (
                <DogCard
                    dog={dog}
                    key={dog.id}
                    selectDog={setUser}
                    secondaryState={user.selectDog && !dog.inRace}
                    isWallet={true}
                    active={user.selectDog === dog}
                />
            ))}
        </StyledYourDogs>
    );
};

export default React.memo( YourDogs );