import styled from "styled-components";
import {baseTheme} from "../../styles/baseTheme";

import dogBg from "../../assets/img/dogs/dogBg.svg";
import Alert from "../../assets/img/dogs/alert.svg";
import CheckMark from "../../assets/img/icons/checkMark.svg";
import InRace from "../../assets/img/dogs/inRace.svg";

export const StyledDogCard = styled.div`
  width: 23%;
  margin-right: 12px;
  background-color: #161629;
  border: 1px solid #3C3C54;
  border-bottom-width: 4px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  height: 124px;
  transition: .3s opacity;
  margin-bottom: 8px;
  font-family: ${baseTheme.fonts.primary};
  animation: fadeIn 0.3s forwards ease-in-out;

  .left-col {
    width: 45%;
    height: 100%;
    padding: 10px 0;
    box-sizing: border-box;

    .flexNoWrap {
      display: flex;
      margin-bottom: 12px;

      .rating {
        ${baseTheme.mixins.classButton("14px")};
        margin-right: 7px;
        margin-left: 12px;
        white-space: nowrap;
        width: initial;
        padding-right: 8px;
        padding-top: 0;
        padding-bottom: 20px;
      }

      .winRate {
        display: flex;

        svg {
          position: relative;
          top: 3px;
          margin-right: 2px;
        }
      }
    }

    .name {
      background: #2D2D42;
      border: 2px solid #3C3C54;
      box-sizing: border-box;
      box-shadow: 4px 4px 4px #1B1B2D;
      border-radius: 0 0 12px 0;
      width: 92px;
      height: 31px;
      padding: 6px 14px 0;
      font-family: ${baseTheme.fonts.primary};
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 20px;
      text-transform: uppercase;
      color: #FFFFFF;
      position: relative;
      left: -1px;
    }

    button {
      cursor: pointer;
      border: none;
      padding: 4px 12px;
      border-radius: 12px 0;
      background-color: ${baseTheme.colors.secondary};
      margin-left: 12px;
      margin-top: 12px;
      font-family: ${baseTheme.fonts.secondary};
      font-style: normal;
      font-weight: 500;
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      letter-spacing: -0.03em;
      color: #FFFFFF;
      transition: .3s;
      &:hover {
        filter: brightness(120%);
      }
    }
  }
  .dog__nft {
    background-image: url(${dogBg});
    background-repeat: no-repeat;
    background-position-x:center;
    width: 50%;
    svg {
      margin: 0 auto;
      width: 100%;
    }
  }
  &:hover:not(.secondaryState) {
    border-color: #2e2b73;

    &.active {
      border-color: #2825BF;
    }
  }

  &.active {
    border-color: #2825BF;
    box-shadow: 0 14px 16px rgba(40, 37, 191, 0.35);

    button {
      background-color: #272736;
      color: #5B5B6B;
      pointer-events: none;
    }
  }

  &.inRace, &.secondaryState {
    pointer-events: none;
    background-color: ${(isWallet) => isWallet ? "#161629" : "transparent"};

    button {
      pointer-events: none;
      font-style: normal;
      font-weight: 600;
      font-size: 10px;
      line-height: 14px;
      text-align: center;
      letter-spacing: -0.03em;
      text-transform: uppercase;
      color: #75758B;
      background-color: transparent;
      padding-left: 2px;

      &::before {
        content: url(${Alert});
        position: relative;
        top: 1px;
        margin-right: 5px;
      }
    }
  }
  
  &.secondaryState {
    ${(isWallet) => isWallet ? "pointer-events:initial;" : ""}
    button {
      ${(isWallet) => isWallet ? "color: #2BA866; background-color: transparent; padding-right: 0px;" : ""};
      &::before {
        ${(isWallet) => isWallet ? `content: url(${CheckMark})` : ""};
      }
    }
  }
  &.inRace {
    position: relative;
    ${(isWallet) => isWallet ? "border-bottom-width: 1px;" : ""}
    button {
      color: #75758B;
      &::before {
        content: url(${Alert});
        position: relative;
        top: 1px;
        margin-right: 5px;
      }
    }
    &::after {
      content: "";
      position: absolute;
      pointer-events: none;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-image: url(${InRace});
      background-repeat: no-repeat;
      background-size: cover;
    }
  }
  
  @media ${baseTheme.media.mobile} {
    min-width: 272px;
    height: 161px;
    background-position-y: center;
    .left-col {
      padding: 12px 0 18px;
      .flexNoWrap {
        .rating {
          font-size: 16px;
          width: 59px;
          height: 27px;
        }
        .winRate {
          font-size: 18px;
          align-items: center;
          svg {
            position: static;
          }
        }
      }
        .name {
          width: 100%;
          font-size: 32px;
          padding: 14px 0px 0px 16px;
          width: 118px;
          height: 48px;
        }
      .select {
        font-size: 16px;
        padding: 8px 20px;
      }
    }
    & > svg {
      width: 100%;
    }
  }
`