import styled from "styled-components";
import {baseTheme} from "../../../styles/baseTheme";

export const StyledBanner = styled.div`
  width: 100%;
  height: 182px;
  border-radius: 24px 0;
  background-color: ${baseTheme.colors.secondary};
  color: #fff;
  padding: 40px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin-bottom: 24px;

  h1 {
    font-family: ${baseTheme.fonts.primary};
    font-style: normal;
    font-weight: 600;
    font-size: 56px;
    margin-bottom: 12px;
    text-transform: uppercase;
    line-height: 60px;
  }

  p {
    line-height: 17px;
    max-width: 303px;
    font-family: ${baseTheme.fonts.secondary};
    font-style: normal;
    font-weight: normal;
    font-size: 11px;
  }

  .running-dog {
    pointer-events: none;
  }

  .banner__svg-container {
    img {
      position: absolute;
      pointer-events: none;

      &.circle {
        top: -25px;
        left: 50%;
        transform: translateX(-50%);
      }

      &.square {
        top: 90px;
        left: calc(50% + 100px);
        transform: translateX(-50%);
      }

      &.cross {
        bottom: -40px;
        left: calc(50% - 60px);
        transform: translateX(-50%);
      }
    }
  }

  @media ${baseTheme.media.mobile} {
    flex-direction: column-reverse;
    //flex-wrap: wra
    align-items: flex-start;
    height: 354px;
    padding: 35px 30px 40px;
    
    .running-dog {
      width: 114px;
      height: 145px;
    }

    h1 {
      font-size: 56px;
      line-height: 60px;
    }

    p {
      font-size: 14px;
      line-height: 19px;
      max-width: none;
    }

    .banner__svg-container {
      img {
        &.square {
          top: initial;
          left: initial;
          right: -44px;
          bottom: 20px;
          transform: initial;
        }
        &.cross {
          bottom: -48px;
          left: calc(50% - 90px);
        }
      }
    }
  }
`
