import React, {useState} from "react";
import TitleWallet from "../Sections/Wallet/Title/TitleWallet";
import Filters from "../Sections/Wallet/Filters/Filters";
import YourDogs from "../Sections/Wallet/YourDogs/YourDogs";

import {StyledContainer} from "../uiComponents/Container/StyledContainer";
import useAPI from "../Hooks/useAPI";

const Wallet = ({setShowPopUp}) => {

    const {user, setUser, isMobile} = useAPI();
    const [showFilters, setShowFilters] = useState(false);

    const [filters, setFilters] = useState({
        winRate: [0, 100],
        clas: [],
        sorting: -1,
        search: "",
    });

    const setFilter = (key, val) => {
        setFilters(filters => ({
            ...filters, [key]: val
        }))
    }

    const isUser = Object.keys(user).length;

    return (
        <StyledContainer>

            <TitleWallet user={isUser} setUser={setUser} setShowPopUp={setShowPopUp}/>

            {isUser ?
                <>
                    <Filters className={showFilters ? "filters-opened" : ""} setShowFilters={setShowFilters} showFilters={showFilters} setFilters={setFilters} setFilter={setFilter} isMobile={isMobile} filters={filters}/>
                    <YourDogs className={showFilters ? "filters-opened" : ""} filters={filters} user={user} setUser={setUser}/>
                </>
                : null
            }

        </StyledContainer>
    );
}

export default React.memo(Wallet);