import React from 'react';
import {StyledPlaceItem} from "./StyledPlaceItem";

const PlaceItem = ({placeData}) => {

    return (
        <StyledPlaceItem className={placeData.class}>
                {placeData.position}
                {placeData.nft}
                {placeData.name}
                {placeData.state}
        </StyledPlaceItem>
    );
};

export default React.memo(PlaceItem);