import React from 'react';
import {ReactComponent as Medal} from "../../assets/img/dogs/medal.svg";
import {StyledDogCard} from "./StyledDogCard";
import {countClass} from "../../utils/countClass";
import Svg from "../../utils/svg";

const DogCard = ({secondaryState, dog, active, selectDog, isWallet}) => {
    const clas = countClass(dog.class);

    return (
        <StyledDogCard
            className={"dog-wrapper" + (secondaryState || isWallet ? " secondaryState " : active ? " active " : "") + (dog.inRace ? " inRace " : "")}
        >
            <div className="left-col">
                <div className="flexNoWrap">
                    <span className="rating">{clas}</span> <span
                    className="winRate"><Svg component={<Medal/>}/> {dog.winRate}%</span>
                </div>
                <p className="name">{dog.name}</p>

                { isWallet && !dog.inRace ?
                        <button
                            className="select"
                            disabled={true}
                        >
                            READY TO RACE
                        </button>
                        :
                        <button
                            className="select"
                            onClick={() => selectDog((user) => ({...user, selectDog: dog}))}
                        >
                            {secondaryState ? "LOW CLASS" : dog.inRace ? "IN RACE" : active ? "Selected" : "Select"}
                        </button>
                }
            </div>
            <div className="dog__nft">
                {dog.nft}
            </div>
        </StyledDogCard>
    );
};

export default DogCard;