import React, {useEffect, useState} from 'react';

import Table from "@mui/material/Table";
import Sorting from "./Sorting/Sorting";
import {sorting} from "../../../InfoData/walletFilters";
import {filtersLIB} from "../../../InfoData/tableFilters";
import FilterSelect from "../../../uiComponents/FilterSelect/FilterSelect";
import MobileFilters from "../../../uiComponents/MobileFilters/MobileFilters";

import {StyledFilters} from "./StyledFilters";

import {ReactComponent as FilterSVG} from "../../../assets/img/icons/filter.svg";
import {ReactComponent as Search} from "../../../assets/img/icons/search.svg";

const Filters = ({showFilters, setShowFilters, className, filters, setFilter, isMobile, setFilters}) => {
    const [inputValue, setInputValue] = useState("");
    const [showSorting, setShowSorting] = useState(false);


    const handleInput = (e) => {
        e.preventDefault();
        setInputValue(e.target.value);
        setFilter('search', e.target.value.toLowerCase());
    }

    const checkActiveVariantFilter = (item) => filters.clas.some(filter => filter === item) ? " active" : "";

    const resetFilter = (isAll, isClass) => {
        if(isMobile) {
            if(isAll) setFilters({winRate: [0, 100], clas: [], sorting: -1, search: "",})
            else if (isClass) setFilter("clas", []);
            else setFilter("winRate", [0, 100]);
        }
        else setFilter("clas", []);
    }

    const handleFilters = (item) => {

        const removedArr = (pos) => {
            const {clas} = filters;
            clas.splice(pos, 1);
            return clas;
        };

        const removeOrPush = filters.clas.findIndex(clas => clas === item);

        removeOrPush === -1 ?
            setFilter('clas', [...filters.clas, item])
            :
            setFilter('clas', removedArr(removeOrPush))

        if(isMobile) setShowFilters(true);

    }

    const handleSorting = (i) => {
        setFilter('sorting', i)
        setShowSorting(false);
    }

    const handleClickOutside = (e) => {
        if (e.target.tagName !== 'LI' && !e.target.classList.contains('sorting')) {
            setShowSorting(false);
        }
    }

    const setInitialOptions = (initialOptions) => setFilters(initialOptions);

    useEffect(() => {
        document.addEventListener('click', handleClickOutside, true);
        return () => {
            document.removeEventListener('click', handleClickOutside, true);
        };
    }, []);

    // console.log(filters.clas)

    const filterSelectData = ({
        filters,
        setFilter,
        resetFilter,
        showFilters,
        setShowFilters,
        setInitialOptions,
        filterOptions: filters,
        checkActiveVariantFilter,
        handleFilterVariants: handleFilters,
        activeFilterItems: filtersLIB[3],
    })

    return (

        <>
            <StyledFilters className={className}>
                <div className="top-bar">
                    <div className="left">
                        <label>
                            <input placeholder={"Search by name"} value={inputValue} onChange={(e) => handleInput(e)}/>
                            <Search/>
                        </label>
                        {isMobile ? null :
                            <button onClick={() => setShowFilters(!showFilters)} className={"filter" + ((showFilters || filters.clas.length || !(filters.winRate[0] === 0 && filters.winRate[1] === 100)) ? " active" : "")}>
                                FILTER<FilterSVG/>
                            </button>
                        }
                    </div>

                    {isMobile ?
                        <MobileFilters filterSelectData={filterSelectData} sorting={<Sorting showSorting={showSorting} setShowSorting={setShowSorting} handleSorting={handleSorting} filters={filters}/>} />
                        : <Sorting showSorting={showSorting} setShowSorting={setShowSorting} filters={filters} handleSorting={handleSorting}/>
                    }
                </div>
            </StyledFilters>

            {isMobile ? null :
                <Table>
                    <FilterSelect filterSelectData={filterSelectData}/>
                </Table>}
        </>
    );
};

export default React.memo(Filters);